import React, {Component} from 'react'
import {Route} from 'react-router'

import Products from "../../pages/Products";
import PartnersAndClients from "../../pages/PartnersAndClients"
import TradeMarks from "../../pages/TradeMarks"
import About from "../../pages/About"
import Contacts from "../../pages/Contacts"
// import

const publicPath = '/dist';

export const routeCodes = {
    products: publicPath,
    contacts: `${ publicPath }contacts`,
    about: `${ publicPath }about`,
    tradeMarks:`${ publicPath }trade_marks`,
    partnersAndClients:`${ publicPath }partners_and_clients`
};


 class App extends Component{


    render(){
        return (
            <div>
                <Route exact path={this.props.pathname} component={Products}/>
                {/*<Route exact path={routeCodes.partnersAndClients} component={PartnersAndClients}/>*/}
                {/*<Route exact path={routeCodes.tradeMarks} component={TradeMarks}/>*/}
                {/*<Route exact path={routeCodes.about} component={About}/>*/}
                {/*<Route exact path={this.props.pathname} component={Contacts}/>*/}
            </div>
        )
    }

}

export default App;


